
body,html{margin:0;padding:0;height:100%;overflow:hidden
}
#app{font-family:"Avenir", Helvetica, Arial, sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;color:#2c3e50;margin:0;margin-top:30px;padding:0;height:100%
}
#app .title img{position:fixed;margin-top:-30px;z-index:-1;width:100%
}
#app .img-top{width:300px;margin:0 auto
}
#app .img-top .img-border{display:block;width:300px;height:300px;border:3px solid #ddd;background:#fff;border-radius:5px;-webkit-box-sizing:border-box;box-sizing:border-box;overflow:hidden;position:relative
}
#app .img-top .img-border .img{max-width:300px;min-height:300px;overflow:hidden;clip:rect(0, 0, 300px, 300px)
}
#app .img-top .img-border .hat-con{position:absolute;z-index:10;top:50%;left:50%;width:100px;height:100px;margin:-50px 0 0 -50px
}
#app .img-top .img-border .hat-con .hat-border{position:absolute;border:1px dashed #000;width:100px;height:100px
}
#app .img-top .img-border .hat-con .hat-border .del{position:absolute;z-index:5;bottom:-25px;right:-25px;width:50px;height:50px;-webkit-transform:scale(1) !important;transform:scale(1) !important
}
#app .img-top .img-border .hat-con .hat-border .del .bar{background:#000;border-radius:50%;width:20px;height:20px;line-height:20px;margin:0 auto;margin-top:15px;color:#fff;text-align:center;font-size:12px
}
#app .img-top .img-border .hat-con .imghat{position:absolute;width:100px;height:100px
}
#app .img-top .img-border canvas{width:300px;height:300px
}
#app .img-top .button-group{margin-top:20px
}
#app .img-top .button-group .upload{position:relative;width:180px;margin-right:15px;padding-top:20px
}
#app .img-top .button-group .upload::after{content:"上传图片";position:absolute;top:0;left:0;width:190px;height:40px;display:block;cursor:pointer;background:#586f80;border:2px solid #fff;color:#fff;text-align:center;line-height:40px;border-radius:5px
}
#app .img-top .button-group a{display:inline-block;width:100px;height:40px;float:right;background:#586f80;border:2px solid #fff;color:#fff;text-align:center;border-radius:5px;line-height:40px
}
#app .hat-box{width:100%;height:150px;overflow:hidden
}
#app .hat-box .hat-select{margin-top:10px;width:auto;height:140px;overflow-x:scroll;overflow-y:hidden
}
#app .hat-box .hat-select .hat-border{width:100px;height:100px;display:table-cell
}
#app .hat-box .hat-select .hat-border img{border-radius:5px;margin:10px;padding:5px;-webkit-box-sizing:border-box;box-sizing:border-box;border:2px solid #fff;background:#d8e1f1;width:100px;height:100px
}
#app .download{height:100%;width:100%;background:rgba(1,1,1,0.7);position:absolute;top:0;left:0;z-index:12;text-align:center
}
#app .download img{width:300px;height:300px;border:2px solid #fff;margin-top:30px
}
#app .download p{width:6em;margin:50px auto;color:#fff
}
